  align-items: center;
  background-color: rgb(255, 255, 255);
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s; /* Added transform transition */
}

.option:hover {
  background-color: #405061;
  color: white;
  transform: scale(1.1); /* Makes the option grow by 10% */
}

.option a {
  color: #06d6a0;
}
.selected {
  background-color: #405061;
  color: white;
  transform: scale(1.1); /* Makes the option grow by 10% */

}

.selected a {
  color: #06d6a0;
}

